<template>
  <div>
    <el-container style="height: 700px; border: 1px solid #eee">
      <el-aside width="300px">
        <Navigation></Navigation>
      </el-aside>
      <el-container>
        <el-header style="text-align: center; font-size: 36px">
          {{msg}}
        </el-header>

        <el-main style="text-align: left; font-size: 18px">
          <el-divider content-position="left"></el-divider>
          {{intromsg}}
          <el-divider content-position="left"></el-divider>
          <el-table :data="tableData" stripe="true ">
            <el-table-column type="index" :index="indexMethod1" width="150">
            </el-table-column>
            <el-table-column prop="func" label="" width="250">
            </el-table-column>
            <el-table-column type="index" :index="indexMethod2" width="150">
            </el-table-column>
            <el-table-column prop="funcp" label="" width="250">
            </el-table-column>
          </el-table>
        </el-main>

        <el-footer>
          <Copyright></Copyright>
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Navigation from './navi/Navigation'
import Copyright from './copyright/Copyright'

export default {
  name: "",
  data(){
    return{
      tableData: [
        {
          func: '',
          funcp: ''
        },
        {
          func: '',
          funcp: ''
        },
        {
          func: '',
          funcp: ''
        },
        {
          func: '',
          funcp: ''
        },
        {
          func: '',
          funcp: ''
        },
        {
          func: '',
          funcp: ''
        },
        {
          func: '',
          funcp: ''
        },
        {
          func: '',
          funcp: ''
        }
      ],
      msg: '',
      intromsg: '\xa0\xa0\xa0\xa0\xa0\xa0\xa0() ()() ()93 '
    }
  },
  methods: {
    indexMethod1(index) {
      return index * 2;
    },
    indexMethod2(index) {
      return index * 2 + 1;
    }
  },
  components:{
    Navigation,
    Copyright
  }
}
</script>

<style scoped>

</style>



